@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.fluid_container {
  width: 90vw;
  margin: 1rem auto;
}

section {
  padding-top: 3%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

section h1 {
  font-size: 2.5rem;
  color: aliceblue;
  font-family: "Otomanopee One", sans-serif;
  letter-spacing: 4px;
}

.messages {
  text-align: center;
  color: rgb(218, 152, 152);
}

@media only screen and (max-width: 600px) {
  .fluid_container {
    text-align: center;
  }

  section {
    flex-direction: column;
  }

  section h1 {
    font-size: 1.5rem;
  }
}

.Image_Card {
  border: 5px solid rgb(216, 214, 214);
  border-radius: 5px;
  width: 180px;
  height: 240px;
}

.Card {
  max-width: 240px;
  margin: 1rem;
  transition: 0.5s all;
}

/* .Card:first-child,
.Card:last-child {
  margin-left: 0;
} */

.fill {
  color: rgb(153, 7, 7);
}

button {
  border: none;
  background: transparent;
  color: rgb(189, 189, 189);
  font-size: 1rem;
  padding: 5px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  font-weight: 600;
}

button i {
  font-size: 2rem;
}

.image_container {
  position: relative;
}

.overlay {
  background: rgba(28, 44, 65, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: rgb(255, 253, 253);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.25s;
}

.overlay > * {
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
}

.overlay:hover {
  opacity: 1;
}

.overlay:hover > * {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.Card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
}

.blur {
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
}

.Card p {
  margin: 3px 0;
  font-size: 0.8rem;
  color: aliceblue;
}

.Card .title {
  font-weight: 600;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

@media only screen and (max-width: 600px) {
  .Image_Card {
    width: 100px;
    height: 140px;
  }

  .Card .title {
    font-size: 10px;
    font-weight: 200;
  }
}

.container {
  padding: 2% 0;
}

.row {
  display: flex;
  overflow-x: auto;
  flex-wrap: nowrap;
}

h2 {
  margin: 0 1rem;
}

::-webkit-scrollbar {
  width: 5px;
  height: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(32, 32, 32);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(61, 61, 61);
}

@media only screen and (max-width: 600px) {
  h2 {
    margin-top: 10px;
  }
}

.form_group {
  box-sizing: border-box;
  width: 50%;
  text-align: right;
}

.form_control {
  padding: 10px 20px;
  width: 70%;
  margin-left: auto;
  text-align: left;
}
@media only screen and (max-width: 600px) {
  .form_group {
    text-align: center;
    width: 80%;
  }
}

